<template>
  <div>
    <b-card
      ref="cardAction"
      title="كافة الإعدادات"
    >
      <b-button
        class="m-2"
        size="sm"
        variant="primary"
        @click="addNew()"
      >
        إضافة
      </b-button>
      <b-row>
        <b-col cols="12">
          <b-table
            id="my-table"
            :per-page="perPage"
            :current-page="currentPage"
            head-variant
            hover
            responsive
            :items="getSettings"
            :fields="fields"
            bordered
          >
            <template #head()="data">
              <span
                class="text-primary"
                style="font-size:18px"
              >{{
                data.label
              }}</span>
            </template>

            <template #table-busy>
              <div class="text-center text-dark my-2">
                <b-spinner class="align-middle" />
                <strong> Loading... </strong>
              </div>
            </template>

            <template #cell(id)="row">
              {{ row.item.id }}
            </template>

            <template #cell(image)="row">
              <b-avatar
                size="4rem"
                class="mr-1"
                :src="row.item.image"
              />
            </template>

            <template #cell(name)="row">
              {{ row.item.name }}
            </template>

            <template #cell(type)="row">
              {{ row.item.type === 1 ? 'جوال' : 'لوحة تحكم' }}
            </template>

            <template #cell(actions)="row">
              <b-button
                class="mr-1"
                size="sm"
                variant="primary"
                @click="edit(row.item)"
              >
                تعديل
              </b-button>
              <b-button
                class=""
                size="sm"
                variant="danger"
                @click="confirmDelete(row.item.id)"
              >
                حذف
              </b-button>
            </template>
          </b-table>
        </b-col>
      </b-row>
      <div class="mt-3">
        <b-pagination
          v-model="currentPage"
          :total-rows="rows"
          align="left"
          :per-page="perPage"
          aria-controls="my-table"
        />
      </div>
    </b-card>
    <edit-setting :obj="Obj" />
  </div>
</template>

<script>
import EditSetting from '@/views/admin/setting/EditSetting.vue'
import {
  BRow,
  BCol,
  BTable,
  BButton,
  BPagination,
  BCard,
  BAvatar,
} from 'bootstrap-vue'
import { faEye, faTrash, faPlus } from '@fortawesome/free-solid-svg-icons'
import { library } from '@fortawesome/fontawesome-svg-core'

library.add(faEye, faTrash, faPlus)

export default {
  name: 'AllCategory',
  components: {
    BCard,
    BRow,
    BCol,
    BTable,
    BButton,
    BAvatar,
    BPagination,
    EditSetting,
  },
  data() {
    return {
      Obj: {},
      perPage: 4,
      currentPage: 1,
      add: true,
      busy: true,
      file: null,
      fields: [
        { key: 'key', label: 'الرمز' },
        { key: 'value', label: 'القيمة' },
        { key: 'actions', label: 'العمليات' },
      ],
    }
  },
  computed: {
    rows() {
      return this.getSettings.length
    },
    getSettings() {
      return this.$store.state.setting.settings
    },
  },
  created() {
    this.$store.dispatch('setting/fetch').then(() => {})
  },
  methods: {
    addNew() {
      this.Obj = { id: 'add' }
      // console.log(this.Obj)
      this.$bvModal.show('EditCat')
    },
    edit(data) {
      this.Obj = data
      // console.log(this.Obj)
      this.$bvModal.show('EditCat')
    },
    confirmDelete(id) {
      this.$swal({
        title: 'حذف التصنيف',
        text: 'هل تريد حذف التصنيف',
        icon: 'warning',
        showCancelButton: true,
        confirmButtonText: 'تاكيد الحذف !',
        cancelButtonText: 'الغاء',
        customClass: {
          confirmButton: 'btn btn-primary',
          cancelButton: 'btn btn-outline-danger ml-1',
        },
        buttonsStyling: false,
      }).then(result => {
        if (result.value) {
          this.$store.dispatch('setting/delete', id).then(() => {
            this.$swal({
              icon: 'success',
              title: 'عملية الحذف',
              text: 'تم حذف الاعداد بنجاح',
              customClass: {
                confirmButton: 'btn btn-success',
              },
            })
          })
        }
      })
    },
  },
}
</script>

<style scoped></style>
